html {
  font-size: 16px;
}

body {
  background-color: #f2f2f2 !important;
}

.press-releases__wrapper {
  background-color: #fff;
}

.press-releases__header-wrapper {
  background-color: #fff;
  border-bottom: 1px solid #eee;
}

.press-releases__header-icon {
  color: #083f88;
  font-size: 20px;
  margin-right: 4px;
}

.press-releases__header-text {
  color: #444;
  font-size: 1.2rem;
}

.featured__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
